import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function Square(props) {
    return (
        <button className="square" onClick={props.onClick}>{props.value}</button>
    );
}

class Board extends Component {
    renderSquare(i) {
        const squares = this.props.squares;
        return <Square key={i} value={squares[i]} onClick={() => this.props.onClick(i)} />
    }

    render() {
        let elements = [];
        let key=0;
        for(let i=0; i<3; i++) {
            let element = null;
            let children = [];
        
            for(let j=0; j<3; j++) {
                children.push(this.renderSquare(key));
                key++;
            }
            
            element = React.createElement(
                'div',
                {className: 'board-row', key: key-3}, 
                children
            );
            elements.push(element);
        }

        return (
            <div>
                {elements}
            </div>
        );
    }
}

class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
            xIsNext: true,
            history: [
                {squares: Array(9).fill('')}
            ],
            stepNumber: 0
        }
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber+1)
        const current = history[history.length - 1];
        let squares = current.squares.slice();

        if (squares[i] || calculateWinner(squares)) {
            return;
        }

        squares[i] = this.state.xIsNext ? 'X': 'O';
        this.setState(state => ({
            xIsNext: !state.xIsNext,
            history: history.concat([
                {squares: squares}
            ]),
            stepNumber: history.length
        }));
    }

    jumpTo(step) {
        this.setState({
            xIsNext: (step % 2) === 0, 
            stepNumber: step
        });
    }

    render() { 
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares);
        let status = '';
        
        if (current.squares.every(checkFilled) && !winner) {
            status = 'Game Over: Draw' ;
        } else {
            status = (winner) ? 'Winner is: ' + winner : 'Next Player: ' + (this.state.xIsNext ? 'X': 'O');
        }
          
        const moves = history.map((step, move) => {
            let desc = move ? 
                'Go to move #' + move :
                'Go to game start';

            if (this.state.stepNumber === move) {
                desc = <span style={{color: 'blue', fontWeight: 'bold'}}>{desc}</span>
            }

            return (
                <li key={move}>
                    <button onClick={() => this.jumpTo(move)}>
                        {desc}
                    </button>
                </li>
            );
        });

        return (
            <div className="game">
                <div className="game-board">
                    <Board squares={current.squares} onClick={(i) => this.handleClick(i)} />
                </div>
                <div className="game-info">
                    <div>{status}</div>
                    <ol>{moves}</ol>
                </div> 
            </div>     
        );
    }
}

function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6]
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
}

function checkFilled(square) {
    return (square === 'X' || square === 'O');
}

ReactDOM.render(<Game />, document.getElementById('root'));